import ApplicationController from "./application_controller";

export default class extends ApplicationController {
    /**
     *
     */
    connect() {
        this.targets.forEach(name => {
            document.querySelectorAll(`[name="${name}"]`)
                .forEach((field) =>
                    field.addEventListener('change',  () => this.asyncLoadData())
                );
        });
    }

    /**
     *
     */
    asyncLoadData() {
        const formData = new FormData(this.element.closest('form'));

        window.axios.post(`${this.data.get('async-route')}`, formData, {
            headers: {
                Accept: "text/vnd.turbo-stream.html",
            },
        })
            .then(response => response.data)
            .then(html => Turbo.renderStreamMessage(html))
    }

    /**
     *
     * @returns {any}
     */
    get targets() {
        return JSON.parse(this.data.get('targets'));
    }
}
